<script lang="ts">
    import Header from "@/views/header/Index.svelte";
    import Footer from "@/views/footer/Index.svelte";
    import supabase from "$lib/supabase";
    import { onMount } from "svelte";
    import { formatTime } from "$lib/util";
    import Diago from "@/component/diago.svelte";
    // url id
    let loading = true;
    export let params: { [key: string]: string };
    interface Post {
        id: number;
        title: string;
        date: string;
        excerpt: string;
        content: string;
        link: string; // 
        readTime?: number; // 
    }
    let post: Post | null = null;
    async function getData(id: number) {
        const { data, error } = await supabase
            .from("githubio_list")
            .select("id,title,date,excerpt,content,link,readTime")
            .eq("id", id)
            .get();
        if (error) {
            console.error(" (Error)", error);
            return null;
        }
        post = data[0];
        //
        const currentReadTime = post?.readTime || 0; //  readtime  0
        const newReadTime = currentReadTime + 1;
        const { data: dataup, error: errorup } = await supabase
            .from("githubio_list")
            .eq("id", id)
            .update({ readTime: newReadTime }, { returning: "minimal" });
        if (errorup) {
            console.error(" readtime :", errorup);
        } else {
            console.log("readtime ");
        }
        loading = false;
    }
    onMount(() => {
        //
        getData(Number(params.id));
    });
    function goback() {
        window.location.hash = "#/";
    }
</script>

<Diago diago={loading} />
<Header />
<div class="main">
    <div class="container">
        {#if post}
            <h2>
                {post?.title}
                <button class="btn btn-primary" type="button" on:click={goback}
                    ></button
                >
            </h2>
            <div class="sub-title">
                <p class="date">: {formatTime(post?.date)}</p>
                <p class="readtime">: {Number(post?.readTime) + 1}</p>
            </div>
            <div class="content-html">{@html post?.content}</div>
        {/if}
    </div>
</div>
<Footer />

<style>
    /*  */
    .btn {
        padding: 0.5rem 1rem;
        border-radius: 0.25rem;
        border: 1px solid #ccc;
        border-color: none;
        background-color: #fff;
        cursor: pointer;
        display: block;
        float: right;
    }
    .btn-primary {
        background-color: #007bff;
        color: #fff;
    }
    .sub-title {
        display: flex;
    }
    .sub-title .date {
        font-size: 14px;
        color: #666;
    }
    .sub-title .readtime {
        font-size: 14px;
        color: #666;
        margin-left: 50px;
    }
    .content-html {
        font-size: 18px;
        color: #971701;
        word-wrap: break-word;
        white-space: pre-wrap;
    }
    .content-html :global(*) {
        white-space: pre-wrap;
        word-wrap: break-word;
    }
</style>
